<template>
  <div class="sign" @click="scrollTop" v-if="isShow">
    <img src="../assets/guide.png" alt="sign" />
  </div>
</template>
<script>
import { throttle } from 'lodash';

export default {
  name: 'Sign',
  data() {
    return {
      isShow: true,
    };
  },
  mounted() {
    document.addEventListener('scroll', throttle(() => {
      const scrollTop = document.documentElement.scrollTop
        || window.pageYOffset
        || document.body.scrollTop;
      const topHeight = document.getElementsByClassName('top')[0].offsetHeight;
      this.isShow = scrollTop <= topHeight / 2;
    }, 50));
  },
  methods: {
    scrollTop() {
      this.isShow = false;
      document.documentElement.scrollTop = document.getElementsByClassName('top')[0].offsetHeight;
    },
  },
};
</script>
<style lang="scss" scoped>
.sign {
  display: block;
  width: 60px;
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  animation: bounce-down 1.6s linear infinite;
  cursor: pointer;
}
@-webkit-keyframes bounce-down {
  25% {
    bottom: 10px;
  }
  50%, 100% {
    bottom: 20px;
  }
  75% {
    bottom: 10px;
  }
}
</style>
